import { Component, Input, Renderer2, ViewChild } from '@angular/core';
import { NG_VALUE_ACCESSOR } from '@angular/forms';
import { IFormControlComponent, IFormFocusable, IPlainObject } from '@helix/platform/shared/api';
import { ValueAccessor } from '../../form-builder/value-accessor';
import { ITextFormControlOptions } from '../text-form-control/text-form-control-options.interface';
import { AdaptRxTextfieldComponent } from '@bmc-ux/adapt-angular';
import { RX_RECORD_DEFINITION } from '@helix/platform/record/api';

@Component({
  selector: 'rx-record-instance-form-control',
  templateUrl: './record-instance-form-control.component.html',
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: RecordInstanceFormControlComponent,
      multi: true
    }
  ]
})
export class RecordInstanceFormControlComponent
  extends ValueAccessor<object>
  implements IFormControlComponent, IFormFocusable
{
  @Input()
  options: ITextFormControlOptions;

  @ViewChild('editor', { static: true })
  editor: AdaptRxTextfieldComponent;

  model: IPlainObject = {
    resourceType: RX_RECORD_DEFINITION.resourceTypes.recordInstanceProcessVariable
  };

  constructor(private renderer: Renderer2) {
    super();
  }

  focus(): void {
    this.renderer.selectRootElement(this.editor.inputRef.nativeElement, true).focus();
  }

  onModelChange() {
    this.value = this.model;
  }
}
